@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/roboto/roboto-v27-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/roboto/roboto-v27-latin_cyrillic-500.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* roboto-700 - latin_cyrillic */

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/roboto/roboto-v27-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/roboto/roboto-v27-latin_cyrillic-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

:root {
  --content-width: 1296px;
  --gutter: 15px;
  --mob-padding: 20px;
}

.container {
  max-width: var(--content-width);
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding);
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
}

html {
  font-family: 'Roboto';
  font-size: 16px;
}

body.content-hidden {
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.btn {
  display: inline-block;
  align-items: center;
  padding: 10px 30px;
  border: none;
  border-radius: 50px;
  text-align: center;
  font-weight: normal;
  font-size: 16px;
  text-decoration: none;
  color: #fff;
  appearance: none;
  cursor: pointer;
}

.btn_main {
  background-image: var(--color-gr-main);
}

.btn_secondary {
  background-image: var(--color-gr-secondary);
}

.btn_empty {
  background-color: transparent;
  border: 1px white solid;
}

.burger {
  position: relative;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 25px;
  cursor: pointer;
  transition: 1s;
  z-index: 10;
}

.burger_closed .burger__line:nth-child(1) {
  transform: translate(0, 11px) rotate(45deg);
}

.burger_closed .burger__line:nth-child(2) {
  display: none;
}

.burger_closed .burger__line:nth-child(3) {
  transform: translate(0, -13px) rotate(-45deg);
}

.burger__line {
  height: 2px;
  background: #fff;
  border-radius: 50px;
  transition: 0.3s;
}

.counter {
  position: relative;
  width: 100%;
  height: 6px;
  /* Can be anything */
  margin: 7px 0 5px 0;
  /* Just for demo spacing */
  background: #fff;
}

.counter__value {
  display: block;
  height: 100%;
  background: linear-gradient(94.78deg, #DF5950 11.19%, #451046 93.72%);
  position: relative;
  overflow: hidden;
  width: 40%;
}

.s-title {
  margin: 0;
  font-weight: bold;
  font-size: 48px;
  color: #fff;
}

.s-title_icon {
  padding-left: 110px;
  position: relative;
}

.s-title_icon::before {
  content: '';
  background-image: url(../img/common/ellipse.svg);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  width: 80px;
  height: 80px;
  top: -15%;
  left: 0;
}

.s-title_icon-45deg {
  position: relative;
}

.s-title_icon-45deg::before {
  transform: rotate(45deg);
}

.s-title_icon-135deg {
  position: relative;
}

.s-title_icon-135deg::before {
  transform: rotate(135deg);
}

.s-title_icon-180deg {
  position: relative;
}

.s-title_icon-180deg::before {
  transform: rotate(180deg);
}

.header {
  position: fixed;
  padding-top: 48px;
  padding-right: 80px;
  padding-bottom: 48px;
  padding-left: 80px;
  width: 100%;
  top: 0;
  left: 0;
  transition: 0.3s;
  z-index: 5;
}

.header_fixed {
  background: rgba(120, 120, 121, 0.9);
}

.header__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.header__logo-wrap {
  text-decoration: none;
}

.header__logo {
  width: 40px;
  position: relative;
  z-index: 6;
}

.header__menu {
  display: flex;
  list-style-type: none;
}

.header__menu .menu-item {
  position: relative;
  margin-right: 56px;
}

.header__menu .menu-item:last-child {
  margin-right: 0;
}

.header__menu .menu-item:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 5px);
}

.header__menu .menu-item > a {
  color: var(--color-txt-light);
  font-weight: normal;
  text-decoration: none;
}

.header__menu .sub-menu {
  position: absolute;
  top: 100%;
  padding: 0;
  opacity: 0;
  visibility: hidden;
  list-style-type: none;
  transition: 0.3s;
  background-image: var(--color-gr-main);
  transform: translate(0, -20px);
}

.header__menu .sub-menu > .menu-item {
  line-height: 1.3;
  padding: 10px 10px;
  margin-right: 0;
}

.header__menu .sub-menu > .menu-item:hover {
  background: rgba(120, 120, 121, 0.3);
}

.header__menu .sub-menu > .menu-item:hover > a {
  text-shadow: 2px 1px 3px #4e474a;
}

.header__menu .sub-menu > .menu-item:hover .sub-menu {
  display: block;
  transition: 0.5s;
}

.header__menu .sub-menu .sub-menu {
  display: none;
  position: absolute;
  left: 100%;
  top: -5px;
}

.header__menu .sub-menu .sub-menu .menu-item:hover {
  background: rgba(120, 120, 121, 0.3);
}

.header__cta {
  position: relative;
  margin-right: 30px;
  z-index: 6;
}

.promo {
  padding-top: 200px;
  padding-bottom: 220px;
  background-image: url(./../img/promo/bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: var(--color-txt-light);
}

.promo__title {
  margin: 0;
  margin-bottom: 50px;
  font-weight: bold;
  font-size: 64px;
}

.promo__countdown {
  display: flex;
}

.promo__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 30px;
  margin-right: 30px;
  background-color: var(--color-bg-dark);
}

.promo__card:last-child {
  margin-right: 0;
}

.promo__value {
  font-weight: bold;
  font-size: 36px;
}

.promo__desc {
  font-size: 16px;
  color: var(--color-txt-light);
  opacity: 0.4;
}

.offer {
  padding-top: 36px;
  padding-bottom: 36px;
  background-color: var(--color-bg-dark);
}

.offer__btn {
  display: table;
  margin-right: 80px;
}

.offer__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.offer__sell-block {
  display: flex;
  align-items: center;
  width: 50%;
}

.offer__students-wrap {
  width: 47%;
}

.offer__students {
  display: flex;
  justify-content: space-between;
  color: #fff;
  margin-bottom: 8px;
}

.offer__students:last-child {
  margin-bottom: 0;
}

.offer__students-desc {
  opacity: 0.4;
  margin: 0;
}

.offer__students-amount {
  font-weight: bold;
  margin: 0;
}

.offer__students-info {
  padding: 0;
}

.offer__info-block {
  width: 50%;
}

.offer__result {
  max-width: 100%;
  height: 6px;
}

.offer__money-info {
  display: flex;
  justify-content: space-between;
  color: #fff;
}

.offer__from {
  opacity: 0.4;
  margin: 0;
}

.offer__to {
  opacity: 0.4;
  margin: 0;
}

.about {
  padding-top: 200px;
  padding-bottom: 100px;
  background-color: var(--color-theme);
  align-items: center;
}

.about__row {
  align-items: center;
}

.about__img-block {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% / 12 * 5 - var(--gutter) * 2);
  background-image: var(--color-gr-main);
  padding: 30px;
}

.about__img-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  background-color: #000;
}

.about__img {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -38%);
  width: 100%;
}

.about__info-block {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  max-width: calc(100% * 0.5 - var(--gutter) * 2);
  margin-left: calc(100% * 0.08333 + var(--gutter));
}

.about__title {
  margin-bottom: 45px;
}

.about__desc {
  font-size: 16px;
  line-height: 1.6;
  color: var(--color-txt-light);
}

.result {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: var(--color-theme);
}

.result__title {
  margin-bottom: 49px;
}

.result__subtitle {
  font-size: 16px;
  color: #FFF;
  opacity: 0.5;
  line-height: 1.6;
}

.result__cards-wrap {
  display: flex;
  padding-top: 180px;
}

.result__card {
  width: 25%;
}

.result__value {
  position: relative;
  display: flex;
  align-items: center;
  padding-bottom: 25%;
  max-height: 80px;
}

.result__value::before {
  position: absolute;
  content: attr(data-percent);
  top: -150%;
  font-weight: bold;
  font-size: 36px;
  color: #FFF;
}

.result__value::after {
  position: absolute;
  content: "";
  width: 100%;
  height: var(--percent);
  background-image: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%);
}

.result__desc {
  font-size: 14px;
  color: #FFF;
  opacity: 0.5;
  text-align: left;
  line-height: 1.6;
  margin: 0;
  margin-right: var(--gutter);
}

.footer {
  padding-top: 100px;
  background-color: var(--color-theme);
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__title {
  padding-bottom: 30px;
  text-align: center;
}

.footer__subtitle {
  margin: 0;
  margin-bottom: 60px;
  color: #FFFFFF;
  opacity: 0.5;
  text-align: center;
  line-height: 1.6;
}

.footer__form {
  display: flex;
  margin-bottom: 40px;
  width: 500px;
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 50px;
}

.footer__input {
  padding: 10px 10px 10px 30px;
  width: 100%;
  background-color: transparent;
  color: #fff;
  border: none;
  outline: none;
}

.footer__subscribe-btn {
  display: inline-block;
}

.footer__icons-wrap {
  display: flex;
  padding-bottom: 20px;
}

.footer__icon-link {
  position: relative;
  padding: 18px 13px;
  margin-right: 30px;
}

.footer__icon-link:last-child {
  margin-right: 0;
}

.footer__icon-link::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  position: absolute;
  width: 50px;
  height: 50px;
  background-image: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%);
  background-color: #C89AFC;
  z-index: 2;
  border-radius: 50px;
}

.footer__icon {
  position: relative;
  z-index: 3;
}

.footer__info {
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
  color: #fff;
  opacity: 0.8;
  line-height: 1.6;
}

.benefits {
  padding-top: 100px;
  padding-bottom: 130px;
  background-color: var(--color-theme);
}

.benefits__title {
  text-align: center;
  margin-bottom: 110px;
}

.benefits__row {
  flex-wrap: wrap;
  justify-content: center;
}

.benefits__card {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  max-width: calc(100% * 0.33333 - var(--gutter) * 2);
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
}

.benefits__card-img {
  margin-bottom: 60px;
  height: 100px;
}

.benefits__card-title {
  text-align: center;
  font-weight: 500;
  font-size: 24px;
  color: #fff;
  margin: 0;
  margin-bottom: 30px;
  line-height: 1.4;
}

.benefits__card-desc {
  margin: 0;
  text-align: center;
  margin-top: auto;
  font-size: 16px;
  color: #fff;
  opacity: 0.8;
  line-height: 1.6;
}

.partners {
  padding-top: 100px;
  padding-bottom: 130px;
  background-color: var(--color-theme);
}

.partners__title {
  margin-bottom: 90px;
}

.partners__wrapper {
  overflow: hidden;
  margin: -2px;
}

.partners__card {
  padding: 30px 65px;
  background-color: var(--color-theme);
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.partners__card-img {
  margin-left: auto;
  margin-right: auto;
}

.schedule {
  padding-top: 80px;
  padding-bottom: 130px;
  background-color: var(--color-theme);
}

.schedule__title {
  margin-bottom: 30px;
  text-align: center;
  padding-left: 0;
}

.schedule__title::before {
  position: static;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 75px;
}

.schedule__subtitle {
  margin: 0;
  margin-bottom: 80px;
  text-align: center;
  color: #FFFFFF;
  opacity: 0.5;
  line-height: 1.6;
}

.schedule__content {
  position: relative;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.schedule__content::before {
  content: '';
  position: absolute;
  background-image: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%);
  background-repeat: no-repeat;
  right: 50%;
  height: 100%;
  width: 5px;
  z-index: 2;
}

.schedule__content :nth-child(odd).schedule__card {
  align-items: flex-end;
  text-align: end;
  padding-left: 0;
  padding-right: 350px;
}

.schedule__card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 350px;
  margin-bottom: 5px;
}

.schedule__card::after {
  content: '';
  position: absolute;
  background-color: #fff;
  opacity: 0.3;
  width: 40px;
  height: 1px;
  top: 50%;
  right: 50%;
  z-index: 1;
}

.schedule__card:nth-child(even)::after {
  left: 50%;
}

.schedule__date {
  margin: 0;
  margin-bottom: 15px;
  font-size: 14px;
  color: #FFFFFF;
  opacity: 0.5;
}

.schedule__desc {
  margin: 0;
  position: relative;
  font-size: 18px;
  color: #FFFFFF;
  line-height: 1.6;
}

.teachers {
  padding-top: 100px;
  padding-bottom: 50px;
  background-color: var(--color-theme);
}

.teachers__title {
  padding-bottom: 63px;
}

.teachers__card {
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.teachers__photo {
  max-height: 190px;
  max-width: 190px;
  margin-bottom: 40px;
}

.teachers__name {
  margin: 0;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 500;
  font-size: 24px;
  color: #FFFFFF;
}

.teachers__position {
  margin: 0;
  margin-bottom: 40px;
  text-align: center;
  color: #FFFFFF;
  opacity: 0.8;
}

.teachers__btn:hover {
  background-image: linear-gradient(94.78deg, #DF5950 11.19%, #451046 93.72%);
  border: none;
}

@media (max-width: 1200px) {
  .header {
    padding-left: 20px;
    padding-right: 20px;
  }

  .header__logo {
    width: 30px;
  }

  .header__menu .menu-item {
    margin-right: 20px;
  }

  .offer__btn {
    margin-right: 30px;
  }

  .offer__sell-block {
    justify-content: space-between;
    width: 100%;
    margin-right: 30px;
    margin-bottom: 30px;
    margin-right: 0;
  }

  .offer__info-block {
    width: 100%;
  }
}

