  .offer__row {
    flex-direction: column;
  }

  .offer__sell-block {
    flex-direction: column;
  }

  .offer__students-wrap {
    width: 100%;
  }

  .about {
    padding-bottom: 70px;
  }

  .about {
    padding-top: 80px;
  }

  .about__img-block {
    width: calc(100% /12 * 6);
    margin-left: auto;
    margin-right: auto;
  }

  .about__info-block {
    margin-top: 50px;
    max-width: 100%;
    margin-left: var(--gutter);
    margin-right: var(--gutter);
