  }

  .result__card {
    margin-left: 60%;
    position: relative;
  }

  .result__value {
    justify-content: center;
    max-height: none;
    height: 140px;
    padding-bottom: 0;
  }

  .result__value::before {
    font-size: 18px;
    top: 30%;
    left: 85%;
  }

  .result__value::after {
    width: calc(var(--percent) / 3);
    height: 100%;
  }

  .result__desc {
    position: absolute;
    top: 30%;
    left: -240%;
    right: 85%;
  }

  .benefits {
    padding-top: 60px;
    padding-bottom: 30px;
  }

  .benefits__title {
    margin-bottom: 70px;
  }

  .benefits__card {
    max-width: calc(100% * 0.5 - var(--gutter) * 2);
    margin-bottom: 20px;
  }

  .partners {
    padding-top: 45px;
    padding-bottom: 70px;
  }

  .schedule {
    padding-top: 30px;
    padding-bottom: 80px;
  }

  .teachers {
    padding-top: 40px;
  }
}

