  }

  .about {
    padding-bottom: 50px;
  }

  .about__img-block {
    width: calc(100% /12 * 10);
    padding: 25px;
  }

  .result {
    padding-top: 30px;
  }

  .result__cards-wrap {
    padding-top: 40px;
  }

  .result__value::after {
    width: calc(var(--percent) / 2);
  }

  .result__desc {
    font-size: 13px;
