    font-size: 12px;
  }

  .footer__icon-link::after {
    width: 40px;
    height: 40px;
  }

  .benefits__card {
    max-width: calc(100% * 1 - var(--gutter) * 2);
  }

  .partners__container {
    margin-left: calc(var(--mob-padding) * -1);
    margin-right: calc(var(--mob-padding) * -1);
    overflow: hidden;
  }

  .schedule {
    padding-top: 20px;
    padding-bottom: 50px;
  }

  .schedule__title {
    padding-left: 0;
  }

  .schedule__content {
    max-width: 400px;
  }

  .schedule__content::before {
    right: auto;
  }

  .schedule__content :nth-child(odd).schedule__card {
    padding-right: 0;
    padding-left: 75px;
    align-items: flex-start;
    text-align: start;
  }

  .schedule__card {
    padding-left: 75px;
    margin-bottom: 20px;
  }

  .schedule__card::after {
    left: 0;
  }

  .schedule__card:nth-child(even)::after {
    left: 0;
  }

  .schedule__date {
    margin-bottom: 5px;
  }

  .teachers__container {
    margin-left: calc(var(--mob-padding) * -1);
    margin-right: calc(var(--mob-padding) * -1);
  }

  .teachers__card {
    padding: 30px 15px;
  }

  .teachers__photo {
    max-width: 150px;
  }
}